import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard.component';

import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { LottieModule } from 'ngx-lottie';

import { AccountInformationComponent } from './components/account-information/account-information.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { playerFactory } from 'src/app/app.module';
const routes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    children: [
      {
        path: 'overview',
        loadChildren: () => import('../menu/overview/overview.module').then(m => m.OverviewModule)
      },
      {
        
        path: 'manage',
        loadChildren: () => import('../menu/manage/manage.module').then(m => m.ManageModule)
      },
      {
        path: 'settings',
        loadChildren: () => import('../menu/settings/settings.module').then(m => m.SettingsModule)
      },
      {
        path: 'developers',
        loadChildren: () => import('../menu/developers/developers.module').then(m => m.DevelopersModule)
      },
    ]
  }
]

@NgModule({
  declarations: [
    DashboardComponent,
    AccountInformationComponent,
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    LottieModule.forRoot({ player: playerFactory }),
  ],
})
export class DashboardModule { }
